.flite-nav{
    height: 70px;

    background-color: #003580;
}
.filte-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    height:300px;
    margin:2% 10%;
    width:80%;
    margin-top: -5%;
    color: gray;
}
.card{
    height:250px;
    width:340px;
    padding:10px;
}
.filte-container img{
    height:100%;
    width: 100%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.filte-container h1{
    font-size: 15px;
    text-align: center;
    padding:10px;
}
.Flight-search{
    margin:4% 10%;
    width:80%;
    display: flex;
    
    justify-content: center;
    gap:10px;
    align-items: center;
}
.flight-search-container{
    display: flex;
    align-items: start;
    justify-content: center;
    gap:20px;
    flex-direction: column;

}
.flight-search-container input{
    padding:20px 30px;
    width:100%;
    font-size: 18px;
}

.flight-btn{
    background-color: #003580;
    color: #fff;
    padding: 23px 70px;
    transform: translateY(20px);

}
.api{
    border: 1px solid #003580;
    margin: 40px;
    padding: 20px;
}
.api span{
margin: 20px;
font-size: 17px;
font-weight: normal;
}
.flight-book{
    margin:  5% 10%;
    width: 80%;
    margin-top: 100px;
}

.table {
    font-family: arial, sans-serif;
    width: 100%;
    
}

td,
th {
    
    width: 400px;
    text-align: center;
    padding: 8px;
    font-size: 20px;
}

td {
    background-color: #003580;
    color: #fff;


}
.headeer-container {
    width: 100%;
    padding: 2rem 2rem;
    color: white;
    display: flex;
    align-items: center;
}
.header{
    padding:30px 0;
}


.book-now{
    text-decoration: none;
    color: #fff;
    border:none;
display: hidden;}



.flight{
    padding:10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    gap:20px;
}
.fligth-option{
    height:500px;
    width:250px;
    
    margin: 20px 0;
    border:2px solid #003580;
    border-radius: 10px;
        overflow: hidden;
    box-shadow: 10px 10px rgba(156, 154, 154, 0.5);
}
.btn-book{
    width: 80%;
    margin:0 10%;
    margin-top:30px;
    padding: 4px 7px;
    background-color: #003580;
    color: #fff;
}

.fligth-image img{
    height:180px;
    width:100%;
}
.fligth-details{
    display: flex;
    margin-left:4px ;
    align-items: center;
    justify-content: space-between;
    padding:4px;
    font-weight: bold;
    color: #003580;
    border:none;
}

@media  (max-width: 350px){
    .fligth-details{
        font-size: 12px;
    }
}


.format-pre pre {
    background: #003580;
    color: #f7f7f7;
    padding: 10px;
    font-size: 14px;
}